// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios' // axios
import path from './components/_path.js' // http
import './assets/scss/font-awesome.css' // 
import './assets/scss/reset.css' // 

Vue.config.productionTip = false
/* eslint-disable no-new */
Vue.prototype.path = path
Vue.prototype.$http = axios
Vue.filter('date', function (input) { // -
  let d = new Date(input)
  let year = d.getFullYear()
  let month = d.getMonth() + 1
  let day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate()
  return year + '-' + month + '-' + day
})
Vue.filter('datePoint', function (input) { // .
  let d = new Date(input)
  let year = d.getFullYear()
  let month = d.getMonth() + 1
  let day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate()
  return year + '.' + month + '.' + day
})
Vue.filter('dateText', function (input) { // 
  let d = new Date(input)
  let year = d.getFullYear()
  let month = d.getMonth() + 1
  let day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate()
  return year + '' + month + '' + day + ''
})
Vue.filter('rateOfInterest', function (input) { // 
  switch (input) {
    case 0:
      return ('T+0')
    case 1:
      return ('T+1')
    case 2:
      return ('T+2')
  }
})
Vue.filter('refundStyle', function (input) { // 
  switch (input) {
    case 0:
      return ('')
    case 1:
      return ('')
  }
})
Vue.filter('investStatus', function (input) { // 
  switch (input) {
    case 0:
      return ('')
    case 1:
      return ('')
    case 2:
      return ('')
  }
})
Vue.filter('moneyStatus', function (input) { // 
  switch (input) {
    case 0:
      return ('')
    case 1:
      return ('')
    case 2:
      return ('')
  }
})
Vue.filter('mark', function (input) { // 
  switch (input) {
    case 1:
      return ''
    case 2:
      return ''
  }
})
Vue.filter('id18', function (value) { // 
  if (value) {
    if (value.length === 15) {
      return value.substr(0, 3) + '*********' + value.substr(11, 5)
    } else {
      return value.substr(0, 3) + '*********' + value.substr(14, 5)
    }
  }
})
Vue.filter('thousand', function (input) { // .00
  return Number(input).toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
})
Vue.filter('bankCard', function (val) { // 
  if (val) {
    return val.substr(0, 4) + '****' + '****' + '****' + val.substr(11, 4)
  }
})
Vue.filter('lastNum', function (input) { // 
  return String(input).slice(String(input).length - 4)
})
Vue.filter('deadLine', function (input) { // 
  if (input >= 30) {
    return input / 30
  } else {
    return input
  }
})
Vue.filter('deadWord', function (input) { // 
  if (input >= 30) {
    return ''
  } else {
    return ''
  }
})
Vue.filter('up', function (n) { // 
  let fraction = ['', '']
  let digit = ['', '', '', '', '', '', '', '', '', '']
  let unit = [
    ['', '', ''],
    ['', '', '', '']
  ]
  let head = n < 0 ? '' : ''
  n = Math.abs(n)
  let s = ''
  for (let i = 0; i < fraction.length; i++) {
    s += (digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]).replace(/./, '')
  }
  s = s || ''
  n = Math.floor(n)
  for (let i = 0; i < unit[0].length && n > 0; i++) {
    let p = ''
    for (let j = 0; j < unit[1].length && n > 0; j++) {
      p = digit[n % 10] + unit[1][j] + p
      n = Math.floor(n / 10)
    }
    s = p.replace(/(.)*$/, '').replace(/^$/, '') + unit[0][i] + s
  }
  return head + s.replace(/(.)*/, '').replace(/(.)+/g, '').replace(/^$/, '')
})

new Vue({
  el: '#app',
  router,
  axios,
  components: {
    App
  },
  template: '<App/>'
})



