export default {
  my: { // 
    method: 'get',
    url: '/a/u/user/'
  },
  myMessage: function (id) { // 
    return {
      method: 'get',
      url: '/a/u/user/userMessage/list/' + id
    }
  },
  myHelp: { // 
    method: 'get',
    url: '/a/u/user/help'
  },
  myOur: { // 
    method: 'get',
    url: '/a/u/user/about'
  },
  myIeda: function (id, data) { // 
    return {
      method: 'post',
      url: '/a/u/user/feedback/' + id,
      params: data
    }
  },
  myUpdata: { // 
    method: 'get',
    url: '/a/u/user/Update'
  },
  myUse: function (id) {
    return { // 
      method: 'get',
      url: 'a/u/user/set/' + id
    }
  },
  myWater: { // 
    method: 'get',
    url: '/a/u/transaction-log/list'
  },
  myEmail: function (id, data) { // 
    return {
      method: 'put',
      url: '/a/u/user/set/' + id,
      params: data
    }
  },
  myAddres: function (id, data) { // 
    return {
      method: 'put',
      url: '/a/u/user/set/' + id,
      params: data
    }
  },
  myChangeCode: function (id, data) { // 
    return {
      method: 'put',
      url: 'a/u/user/pasd/' + id,
      params: data
    }
  },
  myBank: { // 
    method: 'get',
    url: '/a/u/Bank'
  },
  myAutoCard: function (id, idCard) { // 
    return {
      method: 'post',
      url: '/a/u/defultCard/' + id,
      params: idCard
    }
  },
  myRealCard: function (id, params) { // 
    return {
      method: 'post',
      url: '/a/u/userImage/Upload/' + id,
      data: params,
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    }
  },
  out: {
    method: 'get',
    url: '/a/u/user/logout'
  },
  myAddBankG: function (id) { // 
    return {
      method: 'get',
      url: '/a/u/userBank/' + id
    }
  },
  myAddBank: { // 
    method: 'get',
    url: '/a/u/Bank'
  },
  myAddBankCard: function (id, params) { // 
    return {
      method: 'post',
      url: '/a/u/userBank/' + id,
      params: params
    }
  },
  note: function (id) { // 
    return {
      method: 'post',
      url: '/a/register/code',
      params: id
    }
  },
  code: function (param) { // 
    return {
      method: 'post',
      url: '/a/register',
      params: param
    }
  },
  reCode: function (param) { // 
    return {
      method: 'post',
      url: '/a/user/pswd',
      params: param
    }
  },
  banner: { // 
    method: 'get',
    url: '/a/content/banner'
  },
  invest: function (num) { // 
    return {
      method: 'get',
      url: '/a/u/transaction/list/' + num
    }
  },
  enter: function (params) {
    return {
      method: 'post',
      url: '/a/login',
      params: params
    }
  },
  product: { // 
    method: 'get',
    url: '/a/product/list'
  },
  // 
  continueList: {
    method: 'get',
    url: '/a/u/transaction/list/continue'
  },
  // 
  productDetail: function (id) {
    return {
      method: 'get',
      url: '/a/product/' + id
    }
  },
  // 
  inputMoney: {
    method: 'get',
    url: '/a/u/r/pay/investment'
  },
  // 
  investDetail: function (id) {
    return {
      method: 'get',
      url: '/a/u/transaction/' + id
    }
  },
  // 
  contractNot: function (type) {
    return {
      method: 'get',
      url: '/a/u/contract/unsigned/' + type
    }
  },
  // 
  contractHave: function (id) {
    return {
      method: 'get',
      url: '/a/u/contract/' + id
    }
  },
  // 
  contractWait: {
    method: 'get',
    url: '/a/u/contract/ready-signed'
  },
  // 
  Upwrite: function (data) {
    return {
      method: 'post',
      url: '/a/u/r/pay/uploadimg',
      data: data,
      headers: {
        'Content-Type': undefined
      }
    }
  },
  // 
  payMoney: function (params) {
    return {
      method: 'post',
      url: '/a/u/r/pay/contract',
      params: params
    }
  },
  // 
  successPay: function (id) {
    return {
      method: 'get',
      url: '/a/u/r/pay/transaction/' + id
    }
  },
  // 
  continueContract: function (params) {
    return {
      method: 'post',
      url: '/a/u/r/pay/continue',
      params: params
    }
  }
}



