<template>
    <div class="contract-view bte8 container">
        <transition name="slide-fade">
            <my-canvas class="bte8" :writeShow='writeShow' v-if="writeShow"></my-canvas>
        </transition>
        <borrow-contract :contractShow='contractShow' :contract='contract' :today='today'></borrow-contract>
        <out-contract :contractShow='contractShow' :contract='contract' :underLine='underLine'></out-contract>
        <litigation-contract :contractShow='contractShow' :contract='contract'></litigation-contract>
        <principal-contract :contractShow='contractShow' :contract='contract' :money='money' :underLine='underLine'></principal-contract>
        <br>
        <div class="tr" v-show="!contractShow.isPrincipal">
            <span> </span>
            <button class="userSign userSign-bg" v-show="isUserSign" :disabled="writeAble" @click="goWrite()"></button>
            <img :src="contract.userSign" class="userSign tc" v-show="!isUserSign" >
        </div>
        <br>
        <div class="tr">
            <span></span>
            <img :src="contract.companyCachet" alt="" class="comSign">
        </div>
        <br>
        <p class="date tr" v-if="contractShow.isPrincipal">
            <span v-if="!underLine">{{contract.contractCreateAt|dateText}}</span>
            <span v-if="underLine">XXXXXXXX</span>
        </p>
        <div class="confirm-box bte8" v-if="isBtn">
            <button :class="[{'btn-grey':isContinue}, 'btn']" @click="confirm()" :disabled="isContinue"></button>
        </div>
        <transition name='fade'>
            <span class="msg" v-if="isMsg"></span>
        </transition>
        <transition name="slide-fade">
            <div class="write-alert alert flex-cent" @click.self="boxHide()" v-if="isWrite">
                <ul class="tc">
                    <li class="write-one bbe8"></li>
                    <li class="write-two"></li>
                    <li class="write-three" @click="writeConfirm()"></li>
                </ul>
            </div>
        </transition>
    </div>
</template>

<script>
import borrow from './borrow'
import out from './out'
import litigation from './litigation'
import principal from './principal'
import write from './write'
export default{
  data () {
    return {
      contract: {},
      id: undefined,
      isUserSign: true, // 
      writeAble: true, // 
      writeShow: false, // 
      isWrite: false, // 
      isContinue: false, // 
      transcationId: undefined, // id
      isMsg: false, // 
      type: undefined, // 
      money: '', //  
      confirm: undefined, // 
      tody: '', // 
      params: {}, // 
      payMsg: {}, // 
      underLine: true, // 
      isBtn: false, // 
      contractShow: { // 
        isBorrow: false, // 
        isOut: false, // 
        isLitigation: false, // 
        isPrincipal: false // 
      }
    }
  },
  created () {
    this.today = new Date().getTime()
    this.getContract()
    switch (this.type) {
      case 2:
        this.contractShow.isBorrow = true
        break
      case 3:
        this.contractShow.isOut = true
        break
      case 4:
        this.contractShow.isLitigation = true
        break
      case 5:
        this.contractShow.isPrincipal = true
    }
    if (this.type && this.id === undefined) { // 
      this.isUserSign = true // 
      this.writeAble = true // 
      this.$http(this.path.contractNot(this.type)).then(res => { // type
        console.log(res.data.code)
        if (res.data.code === 0) {
          this.contract = res.data
          this.underLine = true // 
        }
        console.log(this.contract, '')
      })
    } else if (this.id && this.type | this.transcationId) { // 
      this.isUserSign = false // 
      this.$http(this.path.contractHave(this.id)).then(res => { // id
        console.log(res.data.code)
        if (res.data.code === 0) {
          this.contract = res.data.data
        }
        console.log(this.contract, '')
        this.params = {id: this.transcationId, userSign: this.contract.userSign}
        console.log(this.params)
      })
      if (this.transcationId) { // 
        this.isBtn = true // 
        this.confirm = () => { // 
          this.$http(this.path.continueContract(this.params)).then(res => { // id
            console.log(res.data, '')
            this.isContinue = true // 
            this.isMsg = true // 
            setTimeout(
              () => { this.isMsg = false }, 4000)
          })
        }
      } else { // 
        this.isBtn = false // 
      }
    } else { // 
      this.isBtn = true // 
      this.isUserSign = true // 
      this.writeAble = false // 
      this.contractShow.isBorrow = true // 
      this.isWrite = true // 
      console.log('')
      this.$http(this.path.contractWait).then(res => {
        console.log(res.data.code)
        if (res.data.code === 0) {
          this.contract = res.data.data
          console.log(this.contract)
        }
      })
      console.log(this.contract.userSign)
      if (this.contract.userSign === undefined) { // 
        this.confirm = () => { // 
          this.isWrite = true
        }
      }
    }
    this.$parent.head.title = ''
    this.$parent.head.isWhite = false
    this.$parent.head.isEnter = false
    this.$parent.head.isArrow = false
  },
  methods: {
    boxHide () { // 
      this.isWrite = false
    },
    writeConfirm () { // 
      this.isWrite = false
    },
    getContract () { // id
      this.id = this.$route.query.id
      this.type = Number(this.$route.query.type)
      this.money = this.$route.query.money
      this.transcationId = this.$route.query.transcationId
      this.payMsg = this.$route.params
      console.log('' + this.type, 'id' + this.id, '' + this.money, 'id' + this.transcationId)
      console.log(this.contractShow)
      console.log(this.$route.query)
      console.log(this.$route.params)
    },
    goWrite () {
      this.writeShow = true
    }
  },
  components: {
    'borrow-contract': borrow,
    'out-contract': out,
    'litigation-contract': litigation,
    'principal-contract': principal,
    'my-canvas': write
  }
}

</script>

<style lang="scss" scoped>
.contract-view {
  @include center($head-h, 0);
  padding-bottom: 0.65rem;
  font-family: "Microsoft YaHe";
  line-height: 0.2rem;
  font-size: 0.1rem;
  .comSign {
    width: 1rem;
  }
  .userSign {
    display: inline-block;
    width: 1rem;
  }
  .userSign-bg{
     line-height: .3rem;
     background: #ccc;
  }
  .date {
    padding-right: 0.92rem;
  }
  .confirm-box {
    position: fixed;
    padding: 0.1rem;
    bottom: 0;
    left: 0;
    right: 0;
    background: #fff;
    .btn {
      width: 100%;
    }
  }
  .msg {
    position: fixed;
    bottom: 0.7rem;
    left: 50%;
    width: 2.1rem;
    transform: translateX(-50%);
  }
  .write-alert {
    .tc {
      width: 3rem;
      background: #fff;
      border-radius: 10px;
      .write-one {
        line-height: 0.5rem;
        font-size: 0.17rem;
        font-family: "Adobe Heiti Std";
      }
      .write-two {
        line-height: 0.7rem;
        color: $tip-color;
      }
      .write-three {
        line-height: 0.4rem;
        font-size: 0.17rem;
        color: #fff;
        background: #e4c471;
        border-radius: 0 0 10px 10px;
      }
    }
  }
}
</style>



